import React from 'react'
import logo from "../../images/logo.png"
import "./footer.scss"
import TelegramIcon from '@mui/icons-material/Telegram';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';

const Footer = () => {
  return (
    <div id='bandQilish' className='footer'>
        <div className="logo">
            <img src={logo} alt="Footer Logo" />
        </div>
        <div className="text">
            <p>Barcha huquqlar himoyalangan <br /> @2021 Toshkuch osh markazi</p>
        </div>
        <div className="icons">
          <button><TelegramIcon className='icons'/></button>
          <button><a href="http://instagram.com//toshkuch_oshmarkaz"><InstagramIcon className='icons'/></a></button>
          <button><FacebookIcon className='icons'/></button>
        </div>
    </div>
  )
}

export default Footer;