import React, { useState } from "react";
import { useCart } from "react-use-cart";
import "./menular.scss";

function Menu({ menuItem }) {
  const { addItem, getItem, removeItem } = useCart();

  return (
    <div className="menular">
      <div className="row w-100 section">
        {menuItem.map((item) => {
          return (
            <div
              className="col-11 col-md-6 col-lg-3 mx-0 mb-4 box"
              key={item.id}
            >
              <div className="card p-0 overflow-hidden h-100 shadow">
                <img src={item.img} className="card-img" alt="Menu_image" />
                <div className="card-body">
                  <h5 className="card-title">{item.title}</h5>
                  <p className="card-text">{item.desc}</p>
                  <h6 className="card-price fs-6">{item.price} 000 so'm</h6>
                  {!getItem(item.id) ? (
                    <button
                      onClick={() => {
                        addItem(item);
                      }}
                      className="btna btn-secondary"
                    >
                      Savatga Qo'shish
                    </button>
                  ) : (
                    <button
                      onClick={() => {
                        removeItem(item.id);
                      }}
                      className="btna btn-secondary"
                    >
                      Savatdan Olish
                    </button>
                  )}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default Menu;
