import React, { useState } from "react";
import "./bandQilish.scss";
import image from "../../images/BandQilish-image.png";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const BandQilish = () => {
  const [name, setName] = useState("");
  const [telephone, setTelephone] = useState("");
  const [data, setData] = useState("");
  const [time, setTime] = useState("");
  const [pep, setPep] = useState("");

  const checkInputs = () => {
    const checker = !!telephone && !!name && !!data && !!time && !!pep;
    return checker;
  };

  const notify = () => toast.success("    ");
  const BandQilish = () => {
    if (!checkInputs()) return;
    axios
      .post(
        `https://api.telegram.org/bot5468643954:AAEE-W5tBEstDejOOpa5ZI4kNnNBDPkDzxs/sendMessage?chat_id=-1001607615932&text=${encodeURIComponent(
          ` :  ${name}
        : ${telephone}
        : ${data}
        : ${time}
        .: ${pep}
      `
        )}&parse_mode=html`
      )

      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });

    notify();
    setName("");
    setTelephone("");
    setData("");
    setTime("");
    setPep("");
  };
  return (
    <div>
      <div className="band-qlish">
        <div className="texts">
          <h1>Stol band qilish</h1>
          <div className="long-inputs">
            <div className="first">
              <span>Ism</span>
              <input
                value={name}
                required
                onChange={(e) => setName(e.target.value)}
                name="name"
                type="name"
                placeholder="Ismingizni yozing"
              />
            </div>
            <div className="first">
              <span>Tel:</span>
              <input
                value={telephone}
                onChange={(e) => {setTelephone(e.target.value)}}
                required
                name="phone"
                type="tel"
                pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}"
                placeholder="+998(__) ___-__-__"
              />
            </div>
          </div>
          <div className="short-inputs">
            <div className="descs">
              <label>Sana</label>
              <input
                value={data}
                onChange={(e) => setData(e.target.value)}
                required
                type="date"
                id="start"
                name="trip-start"
                min="2022-01-01"
                max="2022-12-31"
                placeholder="2022-10-03"
              />
            </div>
            <div className="descs">
              <label>Vaqt</label>
              <input
                min="09:00"
                max="20:00"
                type="time"
                value={time}
                onChange={(e) => setTime(e.target.value)}
                required
                name="time"
                placeholder="19.00"
              />
            </div>
            <div className="desc">
              <label>Mehmonlar</label>
              <input
                type="count"
                value={pep}
                onChange={(e) => setPep(e.target.value)}
                required
                name="count"
                placeholder="4 Kishi"
              />
            </div>
          </div>
          <button
            onClick={() => {
              BandQilish();
            }}
            className="btna btn-outline-light"
          >
            Band Qilish
          </button>
        </div>
        <div className="img">
          <img src={image} alt="Band_Qilish" />
        </div>
      </div>
      <ToastContainer
        position="bottom-left"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </div>
  );
};

export default BandQilish;
