import React from "react";
import "./haqida.scss";
import { Player } from "video-react";
import Chustiy from "../../images/Chustiy-pic.png";
import pic from "../../images/Haqida-pic.png";
import { Reveal } from "react-reveal";
import video from "../../images/Coocking.mp4"

const Haqida = () => {
  return (
    <div className="haqida">
      <Reveal effect="fadeinup">
        <div className="texts">
          <h1>Restoranimiz haqida</h1>
          <div className="desc">
            <p className="haqidaDesc">
              Aksu Shedevr filialimizda ozingizning yoki farzandlaringizni
              tugilgan kunini tantanali va esda qolarli otkazish uchun ajoyib
              imkoniyat!
            </p>
            <p className="haqidaDesc">
              Aksu restoranlarining barcha filiallari ozida milliylikni aks
              etgan bejirim va chiroyli interyer dizaynga ega!
            </p>
          </div>
        </div>
      </Reveal>
      <Player
        className="player"
        autoPlay="true"
        playsInline
        poster="/assets/poster.png"
        src={video}
      />
      <div className="ender">
        <div className="img">
          <img src={Chustiy} alt="" />
        </div>
        <div className="texts-img">
          <h1>Bizning jamoa</h1>
          <p>
            Bizda kop yillik tajribaga ega oshpazlar jamoasi ishlashadi.
            Dunyodagi eng kop oshni tayyorlab Ginnes rekordlar kitobiga kirgan
            Baxriddin Chustiy oshpazlar jamosiga boshchilik qiladi.
          </p>
          <img src={pic} alt="" />
        </div>
      </div>
    </div>
  );
};

export default Haqida;
