import React, { useState } from "react";
import { useCart } from "react-use-cart";
import "./savat.scss";
import Navbar from "../../components/Navbar/Navbar";
import picture from "../../images/empty-cart.png";
import { Link } from "react-router-dom";
import Footer from "../../components/Footer/Footer";
import DeleteIcon from "@mui/icons-material/Delete";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  background: "black",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const Savat = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  let total = 0;

  const [name, setName] = useState("");
  const [tel, setTel] = useState("");
  const [map, setMap] = useState("");

  const notify = () => toast.success("   ");
  const not = () => toast.success("   ");

  const {
    isEmpty,
    emptyCart,
    items,
    updateItemQuantity,
    removeItem,
  } = useCart();

  const checkInputs = () => {
    const checker = !!name && !!tel && !!map;
    return checker && not;
  };

  const Dostavka = () => {
    if (!checkInputs()) return;
    axios
      .post(
        `https://api.telegram.org/bot5414329458:AAFD7q-7d2_Y9NQJhjMF7AcAWPWFzT-PGbM/sendMessage?chat_id=-1001676288832&text=${encodeURIComponent(
          `
            <b>Buyurtma: ${name}</b>
            <b>Tel: ${tel}</b>
            <b>Manzil: ${map}</b>
            ${items
              .map((item) => {
                return `
                <b>${item.title}</b>
                ${item.quantity} x ${item.price} = ${
                  item.quantity * item.price
                } UZS
              `;
              })
              .join("")}
              <b>:</b>${total} UZS
          `
        )}&parse_mode=html`
      )

      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
    notify();
    setName("");
    setTel("");
    setMap("");
  };

  if (isEmpty)
    return (
      <div className="empty">
        <img src={picture} alt="" />
        <div style={{ textAlign: "center" }}>
          <Link to="/menu">
            <button>Go to the Shop</button>
          </Link>
        </div>
      </div>
    );
  return (
    <>
      <div className="cart">
        <Navbar />
        <div className="display">
          <h5>Mahsulot ko'rinishi</h5>
          <h5>Nomi</h5>
          <h5>Narxi</h5>
          <h5>Soni</h5>
        </div>
        {items.map((item) => {
          const pricer = item.quantity * item.price;
          total += pricer;

          if (item.quantity >= 1) {
            return (
              <div className="cart-Items">
                <img src={item.img} alt="" />
                <h6 className="name">{item.title}</h6>
                <h6>{item.price} 000 so'm</h6>
                <div className="buttonss">
                  <button
                    className="minus"
                    onClick={() =>
                      updateItemQuantity(item.id, item.quantity - 1)
                    }
                  >
                    -
                  </button>
                  {item.quantity}x {item.name}
                  <button
                    className="plus"
                    onClick={() =>
                      updateItemQuantity(item.id, item.quantity + 1)
                    }
                  >
                    +
                  </button>
                </div>
                <button className="trash" onClick={() => removeItem(item.id)}>
                  <DeleteIcon />
                </button>
              </div>
            );
          } else {
            removeItem(item.id);
          }
        })}
        <div></div>
        <div className="confirm">
          <h3>Xarid qilishni tasdiqlang</h3>
          <hr />
          <div className="count">
            <h5>Narxi:</h5>
            <p>{total} 000 so'm</p>
          </div>
          <div className="inputs">
            <input
              value={name}
              onChange={(e) => {
                setName(e.target.value);
              }}
              type="text"
              name="name"
              placeholder="Ism"
            />
            <input
              value={tel}
              disabled={!name}
              onChange={(e) => {
                setTel(e.target.value);
              }}
              type="text"
              name="tel"
              placeholder="Telefon raqamingizni kiriting"
            />
            <input
              disabled={!tel}
              value={map}
              onChange={(e) => {
                setMap(e.target.value);
              }}
              type="text"
              name="name"
              placeholder="Manzil"
            />
          </div>
          <button disabled={!map} onClick={handleOpen} className="confirmBtn">
            Tasdiqlash
          </button>
          <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <Box sx={style}>
              <Typography
                style={{ textAlign: "center", color: "#fcca15" }}
                id="modal-modal-title"
                variant="h6"
                component="h2"
              >
                 
              </Typography>
              <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                <button
                  onClick={() => {
                    emptyCart();
                    window.location.reload();
                    Dostavka();
                  }}
                  style={{
                    width: "100%",
                    height: "50px",
                    background: "#fcca15",
                    color: "black",
                    border: "1px solid black",
                    borderRadius: "10px",
                  }}
                >
                  Confirm buy
                </button>
              </Typography>
            </Box>
          </Modal>
        </div>
      </div>
      <ToastContainer
        position="bottom-left"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      <Footer />
    </>
  );
};

export default Savat;
